import React from "react";
import "./RightSidebar.css";
import bottle from "../assets/bottle.svg";
import ItemsList from "./ItemsList";
import { ReactComponent as Shopping } from "../assets/shopping.svg";

const RightSidebar = ({
  changeRoute,
  itemlist,
  deleteButtonHandler,
  reduceButtonHandler,
  increaseButtonHandler,
}) => {
  return (
    <div className="right-container">
      <div className="add-item-container">
        <img className="bottle-icon" src={bottle} alt="Bottle Icon"></img>
        <div className="add-item">
          <p>Didnt find what you need?</p>
          <button type="button" onClick={() => changeRoute("addItem")}>
            Add item
          </button>
        </div>
      </div>
      {itemlist.length === 0 ? (
        <div className="no-item-container">
          <h1>No Items</h1>
          <Shopping className="shopping-icon" />
        </div>
      ) : (
        <ItemsList
          itemlist={itemlist}
          deleteButtonHandler={deleteButtonHandler}
          reduceButtonHandler={reduceButtonHandler}
          increaseButtonHandler={increaseButtonHandler}
        />
      )}

      <div className={"save-list-container"}>
        <div
          className={`save-list-input ${
            itemlist.length !== 0 ? "border-yellow" : null
          } `}
        >
          <input placeholder="Enter a name"></input>
          <button
            className={`save-list-button ${
              itemlist.length !== 0 ? "background-yellow " : null
            } `}
          >
            Save
          </button>
        </div>
      </div>
    </div>
  );
};
export default RightSidebar;
